/** @jsx jsx */
import { Collapse, IconButton, jsx, Text, Box } from "@re-flex/ui/src";
import { useState } from "react";
import { ChevronDown } from "../Icons";
import * as styles from "./styles";
import React from "react";

export const getDefaultValue = ({ defaultValue, type, flowType }) => {
  const propType = flowType ? flowType : type;
  if (!defaultValue || !defaultValue.value) return null;
  if (defaultValue.value === "''") {
    return "[Empty string]";
  }
  if (propType && propType.name === "string") {
    return defaultValue.value.replace(/\'/g, '"');
  }
  if (typeof defaultValue.value === "object" && defaultValue.value.toString) {
    return defaultValue.value.toString();
  }
  return defaultValue.value;
};

export const Prop = ({ propName, prop, getPropType, isToggle }) => {
  const [showing, setShowing] = useState(isToggle || false);
  if (!prop.type && !prop.flowType) return null;

  const toggle = () => setShowing((s) => !s);
  return (
    <React.Fragment>
      <Box>
        <div css={styles.line} data-testid="prop">
          <div css={styles.content}>
            <div
              css={{ ...styles.propName, flex: "0 0 25%" }}
              data-testid="prop-name"
            >
              <Text variant="subtitle2" css={{ textTransform: "none" }}>
                {propName}
              </Text>
            </div>
            <div
              css={{ ...styles.propType, flex: "0 0 25%" }}
              data-testid="prop-type"
            >
              <Text
                variant="body2"
                color="error"
                colorDepth="main"
                css={{ textTransform: "none" }}
              >
                {getPropType(prop)}
              </Text>
            </div>
            {prop.defaultValue ? (
              <div
                css={{ ...styles.defaultValue, flex: "0 0 25%" }}
                data-testid="prop-default-value"
              >
                <Text variant="body2" css={{ textTransform: "none" }}>
                  {getDefaultValue(prop)}
                </Text>
              </div>
            ) : (
              <div css={{ flexGrow: 1 }} />
            )}
            <div css={styles.right}>
              {prop.required && (
                <div css={styles.propRequired} data-testid="prop-required">
                  <strong>required</strong>
                </div>
              )}
              {prop.description && (
                <IconButton
                  css={styles.openDescBtn}
                  onClick={toggle}
                  color={showing ? "primary" : "text"}
                  data-testid="prop-toggle-description"
                  icon={
                    <ChevronDown
                      size={20}
                      css={{
                        transform: `rotate(${showing ? 180 : 0}deg)`,
                        transition: "all .2s ease",
                      }}
                    />
                  }
                />
              )}
            </div>
          </div>
        </div>
      </Box>

      <Collapse open={showing && prop.description}>
        <Box bgColor="body" p={[16, 16, 32, 16]}>
          <Text variant="body2">{prop.description}</Text>
        </Box>
      </Collapse>
    </React.Fragment>
  );
};

export const Props = ({ props, getPropType, isToggle, ...rest }) => {
  const entries = Object.entries(props);
  return (
    <Box bgColor="paper" shadow={1} radius={6} p={[16, 8]} data-testid="props">
      {entries.map(([key, prop]) => (
        <Prop
          key={key}
          propName={key}
          prop={prop}
          getPropType={getPropType}
          isToggle={isToggle}
        />
      ))}
    </Box>
  );
};
