/** @jsx jsx */
import { jsx, List, Collapse } from "@re-flex/ui/src";
import { Link, useCurrentDoc } from "docz";
import React from "react";
import { useNavigate } from "@reach/router";

const getCurrentHash = () => {
  if (typeof window === "undefined") {
    return "";
  }
  return window.location ? decodeURI(window.location.hash) : "";
};

export const NavLink = React.forwardRef(
  ({ item, active, children, ...props }) => {
    const navigate = useNavigate();
    const to = item.route;
    return (
      <>
        <List.Item
          button
          color={active ? "primary" : undefined}
          title={children}
          onClick={() => navigate(to)}
        />
      </>
    );
  }
);

export const PageInLink = React.forwardRef(
  ({ item, active, children, ...props }) => {
    const current = useCurrentDoc();
    const currentHash = getCurrentHash();
    const to = current.route;
    return (
      <div css={{ position: "sticky", top: 0, flex: "0 0 200px", width: 200 }}>
        {Array.isArray(current.headings) &&
          current.headings.map((heading) => (
            <Link to={`${to}#${heading.slug}`} key={heading.slug}>
              <List.Item
                size={4 + 1 / heading.depth}
                button
                key={heading.slug}
                // onClick={() => navigate(`${to}#${heading.slug}`)}
                color={
                  currentHash === `#${heading.slug}` ? "primary" : undefined
                }
                css={{
                  transition: "all .2s ease-in-out",
                  paddingRight: `calc(12px + ${heading.depth / 8}rem)`,
                  borderRight:
                    "2px solid" +
                    (currentHash === `#${heading.slug}`
                      ? "#3f51b5"
                      : "#f9f9f901"),
                  "&>div>span": {
                    textAlign: "right",
                    fontSize: 10 + (1 / heading.depth) * 6,
                  },
                }}
                title={heading.value}
              />
            </Link>
          ))}
      </div>
    );
  }
);
